const AccountTypeIndicator = ({ accountType, customerId }: { accountType: string; customerId?: string }) => {
  const getAccountInfo = (type: string) => {
    switch (type) {
      case 'mcc_sub_account':
        return { title: 'Furriyadh Managed', color: 'text-red-600', commission: '15%' };
      case 'oauth_linked':
        return { title: 'Linked Account', color: 'text-green-600', commission: '12%' };
      case 'hybrid':
        return { title: 'Hybrid Management', color: 'text-purple-600', commission: '10%' };
      default:
        return { title: 'Unknown', color: 'text-gray-600', commission: 'N/A' };
    }
  };

  const accountInfo = getAccountInfo(accountType);

  return (
    <div className="text-sm text-gray-600 mb-4">
      <span className={`font-medium ${accountInfo.color}`}>{accountInfo.title}</span>
      {customerId && <span className="ml-2">({customerId})</span>}
      <span className="ml-2 text-xs bg-gray-100 px-2 py-1 rounded">{accountInfo.commission} Ø¹Ù…ÙˆÙ„Ø©</span>
    </div>
  );
};

